import { categoriesCollection, firebase, likedProductsCollection, productsCollection, storageRef } from '@/firebase'
import productService from '@/services/productService'

export default {
  state: {
    filters: {
      title: "",
      location: "",
      categoryId: "",
      priceFrom: 0,
      priceTo: 0
    }
  },
  actions: {
    async addProduct(context, {product, imageFiles}) {
      return new Promise((resolve, reject) => {
        const currentUser = context.getters.getCurrentUser

        if (currentUser && currentUser.uid) {
          storageRef.constructor.prototype.putFiles = function(files) {
            const ref = this;
            return Promise.all(files.map(function(file) {
              return ref.child(file.name).put(file.data, file.metadata).then((snapshot) => snapshot.ref.getDownloadURL());
            }));
          }

          storageRef.putFiles(imageFiles).then(function(imageUrls) {
            product.imageUrls = imageUrls;

            // save product
            const productData = { uid: currentUser.uid, createdAt: new Date(), ...product }

            productsCollection.doc().set(productData)
              .then(() => resolve())
              .catch(error => reject(error));
          })
        } else {
          throw new Error("Something went wrong. Can not add product.");
        }
      })
    },
    async getProducts(context, {filters, likedOnly}) {
      let query = productsCollection;

      const title = filters?.title;
      const location = filters?.location;
      const categoryId = filters?.categoryId;

      // add filtering to query if any filter exist
      if (filters && (title || location || categoryId)) {
        if (title) {
          query = query.where('title', '>=', title).where('title', '<=', title +'\uf8ff')
        }

        if (location) {
          query = query.where('location', '==', location);
        }

        if (categoryId) {
          query = query.where('categoryId', '==', categoryId);
        }
      }

      let snapshot;

      // if likedOnly = true - load only liked products
      if (likedOnly) {
        const likedProducts = await context.dispatch("getLikedProductsIds");

        if (!likedProducts?.length) return [];

        // load only products with ids form likedProducts array
        snapshot = await query.where(firebase.firestore.FieldPath.documentId(), 'in', likedProducts).get();
      } else {
        // load all products
        snapshot = await query.get();
      }

      if (snapshot.empty) {
        return [];
      }

      let productsList = [];

      snapshot.forEach(doc => {
        // if likedOnly = true - all products already liked, no need to check
        productsList.push({id: doc.id, isLiked: likedOnly, ...doc.data()})
      })

      productsList = productService.filterProducts(productsList, filters);

      // if loading all products and user is authenticated - load liked products ids
      // and check if every products is liked by current user
      if (!likedOnly && context.getters.isAuthenticated) {
        const likedProducts = await context.dispatch("getLikedProductsIds");

        if (likedProducts?.length) {
          productsList = productsList.map(product => {
            product.isLiked = likedProducts.some(likedId => likedId === product.id);

            return product;
          })
        }
      }

      return productsList;
    },
    async getCategories() {
      const snapshot = await categoriesCollection.get();

      if (snapshot.empty) {
        return;
      }

      const categoriesList = [];

      snapshot.forEach(doc => {
        categoriesList.push({ id: doc.id, ...doc.data() })
      })

      return categoriesList;
    },
    async likeProduct(context, productId) {
      const likedIds = await context.dispatch("getLikedProductsIds");

      const currentUser = context.getters.getCurrentUser
      const likedRef = likedProductsCollection.doc(currentUser.uid);

      if (!likedIds) {
        await likedRef.set({productIds: [productId]})

      } else {
        const index = likedIds.findIndex(id => productId === id);

        if (index > -1) return;

        likedIds.push(productId)

        await likedRef.update("productIds", likedIds);
      }
    },
    async removeLikedProduct(context, productId) {
      const likedIds = await context.dispatch("getLikedProductsIds");

      const currentUser = context.getters.getCurrentUser

      if (likedIds) {
        const index = likedIds.findIndex(id => productId === id);

        if (index > -1) {
          likedIds.splice(index, 1);

          await likedProductsCollection.doc(currentUser.uid).update("productIds", likedIds);
        }
      }
    },
    async getLikedProductsIds(context) {
      const currentUser = context.getters.getCurrentUser

      if (currentUser && currentUser.uid) {
        const doc = await likedProductsCollection.doc(currentUser.uid).get();

        if (!doc.exists) {
          return null;
        }

        return doc.data().productIds;
      } else {
        throw new Error("Something went wrong. Can not like product.");
      }
    }
  },
  mutations: {
    setFilters(state, filters) {
      state.filters = { ...filters };
    }
  },
  getters: {
    getFilters(state) {
      return state.filters;
    }
  }
}
