export default {
  filterProducts(products, filters) {
    let resultList = [...products];

    const priceFrom = filters?.priceFrom;
    const priceTo = filters?.priceTo;

    const priceFromExist = priceFrom >= 0.1;
    const priceToExist = priceTo > 0;

    if (priceFromExist || priceToExist) {
      if (priceFromExist && !priceToExist) {
        resultList = products.filter(product =>  product.price >= priceFrom);

      } else if (!priceFromExist && priceToExist) {
        resultList = products.filter(product => product.price <= priceTo);

      } else {
        resultList = products.filter(product => product.price >= priceFrom && product.price <= priceTo);
      }
    }

    return resultList;
  }
}
