<template>
  <section class="filter-panel bg-white">
    <ul class="filter-list">
      <li class="category-filter">
        <category-select @change="filters.categoryId = $event.target.value"></category-select>
      </li>
      <li class="price-filter">
        <input v-model="filters.priceFrom" class="input-base" type="text" placeholder="Price from (USD)">
        <span class="line"></span>
        <input v-model="filters.priceTo" class="input-base" type="text" placeholder="Price to (USD)">
      </li>
    </ul>
  </section>
</template>

<script>
import { mapGetters, mapMutations } from 'vuex'
import mergeObjects from '@/mixins/mergeObjects'
import CategorySelect from '@/components/CategorySelect'

export default {
  name: 'FilterPanel',
  components: { CategorySelect },
  mixins: [mergeObjects],
  computed: {
    ...mapGetters({
      getFilters: 'getFilters'
    })
  },
  data () {
    return {
      filters: {
        categoryId: "",
        priceFrom: 0,
        priceTo: 0
      }
    }
  },
  methods: {
    ...mapMutations({
      setFilters: 'setFilters'
    }),
    updateFilters () {
      const filters = { ...this.getFilters }

      this.setFilters(this.mergeObjects(filters, this.filters))
    }
  },
  watch: {
    filters: {
      handler(){
        this.updateFilters()
      },
      deep: true
    }
  }
}
</script>

<style lang="scss" scoped>

.filter-panel {
  max-width: 860px;
  margin: 20px 4px 0 4px;
  padding: 12px 11px;

  box-shadow: 0px 1px 4px rgba(0, 0, 0, 0.217111);
  border-radius: 4px;

  .filter-list {
    display: flex;
    flex-flow: row wrap;

    & li {
      &:not(:last-child) {
        margin-right: 23px;
      }
    }

    .price-filter {
      display: flex;
      align-items: center;

      .line {
        margin: 0 7px;
        height: 1px;

        display: block;
        width: 12px;
        border: 1px solid #DEDEE0;
      }

      input {
        max-width: 143px;
      }
    }
  }
}

@include breakpoint-to($lg) {
  .filter-panel {
    .filter-list {
      flex-direction: column;

      & li {
        &:not(:last-child) {
          margin-right: 0;
        }
      }

      .category-filter {
        margin-bottom: 10px;
      }

      .category-filter, .category-filter .category-select {
        width: 100%;
      }

      .price-filter {
        input {
          width: 100%;
          max-width: 100%;
        }
      }
    }
  }
}

@include breakpoint-to(430px) {
  .filter-panel {
    margin-right: 6px;
    margin-left: 6px;
  }
}

</style>
