<template>
  <select @change="$emit('change', $event)" class="input-base category-select">
    <option value="" selected>Chose category</option>
    <option v-for="category in categories" :value="category.id" :key="category.id"> {{ category.name }}</option>
  </select>
</template>

<script>

import { mapActions } from 'vuex'

export default {
  name: 'CategorySelect',
  data() {
    return {
      categories: []
    }
  },
  methods: {
    ...mapActions({
      getCategories: 'getCategories'
    })
  },
  created() {
    this.getCategories()
      .then(categories => {
        this.categories = categories;
      })
      .catch(error => {
        console.error(error)
      })
  }
}

</script>

