<template>
  <div class="search-bar-container">
    <ul class="search-bar">
      <li class="search-input">
        <input v-model="filters.title" type="search" placeholder="Search products by name">
      </li>
      <li class="location-input">
        <input v-model="filters.location" type="search" placeholder="Location">
      </li>
      <li class="search-btn-container">
        <button @click="search">SEARCH</button>
      </li>
    </ul>
  </div>
</template>

<script>

import { mapGetters, mapMutations } from 'vuex'
import mergeObjects from '@/mixins/mergeObjects'

export default {
  name: 'SearchBar',
  mixins: [mergeObjects],
  computed: {
    ...mapGetters({
      getFilters: 'getFilters'
    })
  },
  data () {
    return {
      filters: {
        title: '',
        location: ''
      }
    }
  },
  methods: {
    ...mapMutations({
      setFilters: 'setFilters'
    }),
    search () {
      const filters = {...this.getFilters};

      this.setFilters(this.mergeObjects(filters, this.filters));
    }
  }
}

</script>

<style lang="scss" scoped>

.search-bar-container {
  width: 100%;
  margin-top: 44px;

  .search-bar {
    display: flex;

    margin: 0 auto;
    max-width: 855px;

    li {
      &:not(:last-child) {
        margin-right: 7px;
      }

      input {
        width: 100%;
        height: 100%;

        padding: 10px 14px;
        font-size: 15px;
        line-height: 31px;
      }
    }

    .search-input {
      width: 467px;

      input {
        padding-left: 50px;

        background-image: url("../../assets/svg/search-icon.svg");
        background-position: 21px center;
        background-repeat: no-repeat;
      }
    }

    .location-input {
      width: 198px;

      input {
        padding-left: 50px;

        background-image: url("../../assets/svg/location-icon.svg");
        background-position: 21px center;
        background-repeat: no-repeat;
      }
    }

    .search-btn-container {
      display: flex;
      width: 176px;

      button {
        color: white;
        border: 1px solid transparent;
        background: #3E3961;
        border-radius: 4px;
        line-height: 31px;
        padding: 10px;
        width: inherit;
      }
    }
  }
}

@include breakpoint-to($md) {
  .search-bar-container {
    .search-bar {
      flex-flow: row wrap;
      justify-content: space-between;

      li {
        &:not(:last-child) {
          margin-right: 0;
        }
      }

      .search-input {
        width: 100%;

        margin-bottom: 20px;
      }

      .location-input {
        width: 45%;
      }

      .search-btn-container {
        width: 45%;

        button {
          width: 100%;
        }
      }
    }
  }
}

@include breakpoint-to($sm) {
  .search-bar-container {
    .search-bar {
      li {
        &:not(:last-child) {
          margin-right: 0;
        }
      }

      .location-input {
        width: 100%;
        margin-bottom: 20px;
      }

      .search-btn-container {
        width: 100%;
      }
    }
  }
}

</style>
