<template>
  <nav class="nav-bar" :class="{ 'nav-bar-transparent': transparentNav }">
    <div class="nav-content">
      <router-link to="/" class="logo"></router-link>

      <span @click="toggleMenuVisibility" class="menu-icon"></span>

      <div class="actions-container" :class="{'hide': !showNavMenu}">
        <ul class="nav-actions">
          <li v-show="!hideSellButton" class="sell-btn-container">
            <router-link to="/product/add" @click.native="showNavMenu = false" class="sell-btn btn-primary">{{ sellButtonText }}</router-link>
          </li>
          <li v-if="!isAuthenticated" class="login-link-container">
            <router-link to="/login" @click.native="showNavMenu = false" class="login-link">Login</router-link>
          </li>
          <li v-if="isAuthenticated" class="user-profile-link-container">
            <router-link to="/user/profile" @click.native="showNavMenu = false" tag="span" class="user-icon">{{ userInitials }}</router-link>
          </li>
          <li>
            <router-link to="/product/liked" @click.native="toggleMenuVisibility" class="liked-products-link"></router-link>
          </li>
        </ul>
      </div>
      <search-bar v-if="showSearchBar"></search-bar>
    </div>
  </nav>
</template>

<script>
import SearchBar from '@/components/navBar/SearchBar'
import { mapGetters } from 'vuex'

export default {
  name: 'NavBar',
  components: { SearchBar },
  computed: {
    ...mapGetters({
      isAuthenticated: "isAuthenticated",
      currentUser: "getCurrentUser"
    }),
    sellButtonText: function () {
      return this.showAltButtonText ? this.altSellButtonText : this.defaultSellButtonText;
    },
    userInitials() {
      if (this.isAuthenticated) {
        const name = this.currentUser.displayName;
        const fullNameParts = name.split(" ");

        if (fullNameParts?.length > 1) {
          return (fullNameParts[0].substr(0, 1)
            .concat(fullNameParts[1].substr(0, 1))).toUpperCase();
        } if (fullNameParts?.length === 1) {
          return fullNameParts[0].substr(0, 1).toUpperCase();
        }

        return "";
      }
    }
  },
  data() {
    return {
      showNavMenu: false,
      showSearchBar: true,
      transparentNav: false,
      showAltButtonText: false,
      hideSellButton: false,
      defaultSellButtonText: "SELL",
      altSellButtonText: "+ADD"
    }
  },
  methods: {
    updateNav(route) {
      const path = route.path;

      switch (path) {
        case "/":
          this.showAltButtonText = true;
          break
        default:
          this.showAltButtonText = false;
      }

      const meta = route.meta;

      this.showSearchBar = meta.showSearchBar;
      this.transparentNav = meta.transparentNav;
      this.hideSellButton = meta.hideSellButton;
    },
    toggleMenuVisibility() {
      this.showNavMenu = !this.showNavMenu;
    }
  },
  watch: {
    '$route'(newRoute) {
      this.updateNav(newRoute);
    }
  },
  created() {
    this.updateNav(this.$route)
  }
}
</script>

<style lang="scss" scoped>

.nav-bar {
  background: linear-gradient(180deg, #090810 0%, #171236 100%);
  padding: 18px;

  .nav-content {
    display: flex;
    flex-flow: row wrap;
    justify-content: space-between;
    align-items: center;
    margin: 0 auto;
    max-width: 1136px;

    .logo {
      width: 102px;
      height: 42px;
      background: url("../../assets/svg/apiko-logo-white.svg") no-repeat;
    }

    .menu-icon {
      display: none;
    }

    .actions-container {
      .nav-actions {
        display: flex;
        align-items: center;

        li {
          &:not(:last-child) {
            margin-right: 40px;
          }

          a {
            text-transform: uppercase;
            color: #FFFFFF;
            text-decoration: none;
          }
        }

        .sell-btn-container {
          .sell-btn {
            display: block;
            width: 131px;
            padding: 10px;
          }
        }

        .user-profile-link-container {
          .user-icon {
            display: block;
            width: 40px;
            height: 40px;

            line-height: 40px;
            text-align: center;

            border-radius: 100%;
            background: #F1C40F;
          }
        }

        .liked-products-link {
          display: block;
          width: 20px;
          height: 19px;
          background: url("../../assets/svg/like-icon-light.svg") no-repeat center;
        }

        .login-link-container {
          .login-link {
            font-size: 12px;
          }
        }
      }
    }
  }
}

@include breakpoint-to($sm) {
  .nav-bar {
    position: relative;

    .nav-content {
      .menu-icon {
        display: block;
        width: 22px;
        height: 15px;

        background: url("../../assets/svg/menu-icon.svg") no-repeat;
      }

      .actions-container {

        &.hide {
          display: none;
        }

        padding: 10px;
        position: absolute;
        width: 100vw;
        top: 100%;
        left: 0;
        z-index: 999;

        background: linear-gradient(180deg, #171236 0%,#090810 100%);

        .nav-actions {
          flex-flow: row wrap;
          justify-content: space-between;
          align-items: center;

          li {
            padding: 20px 0;

            &:not(:last-child) {
              margin-right: 0;
            }
          }

          .user-profile-link-container {
            width: 60px;
            padding: 10px;
          }

          .liked-products-link {
            background-size: 30px 28.5px !important;
            width: 30px;
            height: 28.5px;
          }

          .sell-btn-container {
            order: 4;
            width: 150px;

            .sell-btn {
              width: 100%;
            }
          }

          .login-link-container {
            width: 100%;
            border-bottom: 1px solid #343434;
            text-align: center;

            .login-link {
              font-size: 14px;
            }
          }
        }
      }
    }
  }
}

.nav-bar-transparent {
  background: transparent;

  .nav-content {
    .logo {
      background: url("../../assets/svg/apiko-logo-dark.svg") no-repeat;
    }

    .actions-container {
      .nav-actions {

        .liked-products-link {
          background: url("../../assets/svg/like-icon-dark.svg") no-repeat center;
        }

        .login-link-container {
          .login-link {
            color: black;
          }
        }
      }
    }
  }

  @include breakpoint-to($sm) {
    .nav-content {

      .menu-icon {
        background: url("../../assets/svg/menu-icon-black.svg") no-repeat;
      }

      .actions-container {
        background: $ap-bg-color;
        border-bottom: 1px solid grey;
      }
    }
  }
}

</style>
