import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '@/pages/Home'
import { auth } from '@/firebase'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home,
    meta: {
      showSearchBar: true
    }
  },
  {
    path: '/login',
    name: 'Login',
    component: () => import(/* webpackChunkName: "login" */ '@/pages/auth/Login'),
    meta: {
      transparentNav: true,
      isAnonymousOnly: true
    }
  },
  {
    path: '/register',
    name: 'Register',
    component: () => import(/* webpackChunkName: "register" */ '@/pages/auth/Register'),
    meta: {
      transparentNav: true,
      isAnonymousOnly: true
    }
  },
  {
    path: '/password/forgot',
    name: 'ForgotPassword',
    component: () => import(/* webpackChunkName: "forgotPassword" */ '@/pages/auth/ForgotPassword'),
    meta: {
      transparentNav: true,
      isAnonymousOnly: true
    }
  },
  {
    path: '/product/add',
    name: 'AddProduct',
    component: () => import(/* webpackChunkName: "addProduct" */ '@/pages/AddProduct'),
    meta: {
      hideSellButton: true,
      requireAuth: true
    }
  },
  {
    path: '/product/liked',
    name: 'LikedProducts',
    component: () => import(/* webpackChunkName: "LikedProducts" */ '@/pages/LikedProducts'),
    meta: {
      hideSellButton: true,
      requireAuth: true
    }
  },
  {
    path: '/user/profile',
    name: 'UserProfile',
    component: () => import(/* webpackChunkName: "userProfile" */ '@/pages/UserProfile'),
    meta: {
      requireAuth: true
    }
  }
]

const router = new VueRouter({
  routes
})

router.beforeEach((to, from, next) => {
  const currentUser = auth.currentUser;
  const requireAuth = to.meta.requireAuth;
  const isAnonymousOnly = to.meta.isAnonymousOnly;

  if (isAnonymousOnly && currentUser) {
    next("/")
  }

  if(requireAuth && !currentUser){
    next("/login");
  }

  next();
});

export default router
